export default{
	props:{
		dataList:{
			type:Array,
			default:[]
		}
	},
	data(){
		return{
			currentIndex:0,
		}
	},
	methods:{
		onClick(v,k){
			if(this.currentIndex==k){
				return
			}else{
				this.currentIndex=k;
				this.$emit('click',{
					type:v.type
				})
			}
		}
	}
}
